import axios from 'axios'
import store from './store'
import router from './router'

// axios
var ajax = axios.create({
    timeout: 10000,         // 10s
    headers: { 'Content-Type': 'application/json;charset=UTF-8' },
})

// 
ajax.interceptors.request.use(
    // 
    config => {
        // TOKENhttp headerTOKEN
        if (store.state.TOKEN) {
            config.headers.Authorization = `token ${store.state.TOKEN}`
        }
        return config;
    }
)

// 
ajax.interceptors.response.use(
    response => {
        return response;
    },
    error => {
        if (error.response) {

            console.log("fullPath:", router.currentRoute.fullPath);
            
            switch (error.response.status) {
                case 401:
                    store.commit('LOGOUT')      // token
                    router.replace({
                        path: 'login',
                        query: { redirect: router.currentRoute.fullPath }   // path
                    })
            }
        }
        
        return Promise.reject(error.response)
    }
)

export default {
    // 
    userLogin(params) {
        return ajax.post('/api/login', params)
    },
    // 
    userRegister(params) {
        return ajax.post('/api/register', params)
    },
    // 
    getUserInfo() {
        return ajax.get('/api/getuserinfo')
    },
    // 
    delUser(params) {
        return ajax.post('/api/deluser', params)
    },
    // 
    upload_file(params) {
        return  ajax.post('/api/upload', params)
    }

}


